@use "../../styles/theme" as theme;
@use "../../styles/sizes" as sizes;

$custom-mobile-width: 540px;
$custom-small-width: 420px;

$avatar-size: 300px;

.homepage-container {
	width: 100%;
	height: 100%;

	.intro {
		width: 100%;
		height: 100vh;
		min-height: 400px;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		position: relative;
		background: linear-gradient(
			theme.$swamp,
			theme.$swamp-lighter,
			theme.$bittersweet
		);

		background-size: cover;
		background-attachment: fixed;

		.avatar {
			height: $avatar-size;
			width: $avatar-size;
			display: flex;
			align-items: center;
			justify-content: center;
			animation: avatar-entry 2s 1;

			img {
				min-width: 100px;
				width: $avatar-size;
				height: $avatar-size;
				border-radius: 100%;
				border: 2px solid theme.$accent;
				box-shadow: 0 5px 10px theme.$accent;
				z-index: 900;
			}
		}

		.buttons-container {
			max-width: 300px;
			display: flex;
			flex-direction: row;
			align-items: center;
			justify-content: space-around;
			z-index: 900;
			animation: avatar-entry 2s 1;

			.button {
				margin: 1em;
				font-size: 18px;
				width: 50%;
				min-width: 140px;
				display: flex;
				flex-direction: row;
				align-items: center;
				justify-content: center;
				z-index: 10;

				svg {
					margin-right: 10px;
				}
			}
		}

		.intro-spacer {
			position: absolute;
			left: 0;
			bottom: 0;
			aspect-ratio: 960/300;
			width: 100%;
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
		}
	}

	section {
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 100%;
		position: relative;

		.content-container {
			width: 700px;
			padding: 2em;

			.sparkle {
				position: absolute;
				font-size: 64px;
				opacity: 1;
				z-index: 1;
				color: theme.$plum;
				opacity: 0.4;
				animation-name: floating;
				animation-duration: 3s;
				animation-iteration-count: infinite;
				animation-timing-function: ease-in-out;
			}

			@keyframes floating {
				0% {
					transform: translate(0, 0px);
				}

				50% {
					transform: translate(0, 15px);
				}

				100% {
					transform: translate(0, -0px);
				}
			}
		}
	}

	.aboutme {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		position: relative;

		& * {
			z-index: 10;
		}

		.title {
			h1 {
				font-size: 4rem;
				text-align: center;
			}
		}

		.text {
			text-align: center;
			font-size: 1.7rem;
		}

		.k-word {
			color: theme.$medium-spring-green;
		}
	}

	.projects {
		width: 90vw;
		margin-bottom: 4em;
		max-width: 1200px;

		.projects-title {
			text-align: center;
			padding: 10px;
			border-radius: 15px;
			backdrop-filter: blur(2px);

			h1 {
				font-size: 4rem;
			}
		}

		.projects-container {
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;
			align-items: center;
			justify-content: center;
			width: 100%;

			.item {
				display: flex;
				flex-direction: column;
				position: relative;
				width: 300px;
				height: 250px;
				margin: 2rem;
				border-radius: 10px;
				transition: 200ms ease-in-out;
				font-size: large;

				&:hover {
					transform: scale(1.1);
					box-shadow: 0 20px 20px -11px rgba(theme.$pictorial-carmine, 0.5);
				}

				.icon {
					position: absolute;
					top: -30px;
					left: 50%;
					transform: translate(-50%);
					width: 100px;
					height: 100px;
					z-index: 100;

					display: grid;
					place-items: center;
					border-radius: 100%;
					overflow: hidden;
					background-color: theme.$swamp;

					img {
						width: 100px;
						height: 100px;
						height: auto;
					}
				}

