				}

				.title {
					text-align: center;
					flex: 1;
					padding: 10px 5px;
					background-color: theme.$swamp-lighter;
					z-index: 110;
					color: theme.$accent;

					h2 {
						margin: 0;
					}
				}

				.description {
					flex: 5;
					height: 50px;
					overflow-y: auto;
					background-color: theme.$swamp-lighter;
					text-align: center;
					padding: 15px;
					width: 100%;
					z-index: 110;
					border-radius: 0 0 8px 8px;
				}
			}
		}
	}
}

@keyframes wipe-enter {
	0% {
		transform: scale(0, 0.25);
	}

	50% {
		transform: scale(1, 0.25);
	}
}

@keyframes avatar-entry {
	from {
		opacity: 0;
		transform: translateY(20px);
	}

	to {
		opacity: 1;
	}
}

@media screen and (max-width: sizes.$mid-width-device) {
	.homepage-container {
		section {
			.content-container {
				width: 95%;
			}
		}
	}
}

@media screen and (max-width: $custom-mobile-width) {
	.homepage-container {
		.aboutme {
			.text {
				font-size: 1rem;
			}
		}
	}
}

@media screen and (max-width: $custom-small-width) {
}
